import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  // base   : '/StartRunWeb/',
  routes: [
    {
      path: '/',
      name: ' HomeVux',
      component: function (resove) {
        require(['./pages/HomeVux.vue'], resove)
      }
    },
    {
      path: '/login',
      name: 'LoginVux',
      component: function (resove) {
        require(['./pages/LoginVux.vue'], resove)
      }
    },
    {
      path: '/BranchWeek',
      name: 'BranchWeek',
      component: function (resove) {
        require(['./pages/BranchWeek.vue'], resove)
      }
    },
    {
      path: '/MyWeek',
      name: 'MyWeek',
      component: function (resove) {
        require(['./pages/MyWeek.vue'], resove)
      }
    },
    {
      path: '/PersonalWeek',
      name: 'PersonalWeek',
      component: function (resove) {
        require(['./pages/PersonalWeek.vue'], resove)
      }
    },
    {
      path: '/ResetPassword',
      name: 'ResetPassword',
      component: function (resove) {
        require(['./pages/ResetPassword.vue'], resove)
      }
    },
    {
      path: '/WeekDetails',
      name: 'WeekDetails',
      component: function (resove) {
        require(['./pages/WeekDetails.vue'], resove)
      }
    }
  ]
})



